// Rules, classes for typepography, fonts etc.
@use "../abstracts/" as *;

/* 
reset default heading typography styles 
*/
h1,
h2,
h3,
h4,
h5,
h6 {
    font: inherit;
}


/*
heading sizing
*/
h1 {
    font-size: var(--fs-500);
}

h2 {
    font-size: var(--fs-400);
}

h3 {
    font-size: var(--fs-300);
}


.heading {
    font-family: var(--ff-heading);
}